
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {mapMutations} from 'vuex'
import Loading from '@/components/Loading'
export default {
  computed: {
    type () {
      return this.$route.params.type
    }
  },
  data () {
    return {
      done: false,
      busy: false,
      height: '',
      class: '',
      sort: false,
      index: 0,
      current: '全部',
      typeList: [],
      list: [],
      typeId: 0,
      showLoading: false,
      select: false,
      bar: [{
        text: '全部',
        key: 'all'
      }, {
        text: '默认',
        key: 'default'
      }, {
        text: '最新',
        key: 'news',
        sort: false
      }, {
        text: '积分',
        key: 'score',
        sort: false
      }],
      options: [],
      option: '全部',
      // 请求的初始参数
      product: {
        type: 0,
        timeOrder: 0,
        priceOrder: 0,
        defaultOrder: 0,
        limit: 8,
        pageIndex: 0
      },
      activeList: [] // 分类商品列表
    }
  },
  mounted () {
    this.height = document.querySelector('.content').clientHeight + 'px'
  },
  components: {
    Tab,
    TabItem,
    Popup,
    Swiper,
    SwiperItem,
    Scroller,
    XImg,
    Group,
    Cell,
    Loading
  },
  created () {
    this.options = JSON.parse(this.$localStorage.get('goodsType'))
    this.options.push({
      id: 0,
      name: '全部'
    })
    this.product.type = this.$route.params.type
    for (const i in this.options) {
      if (this.options[i].id === this.product.type) {
        this.option = this.options[i].name
        console.log(i)
      }
    }
    this.getProduct(this)
  },
  methods: {
    handleChange (index) {
      this.current = this.bar[index].text
      this.index = index
      if (!this.index) {
        this.select = true
      } else {
        this.select = false
        switch (this.index) {
          case 1:
            this.product.timeOrder = 0
            this.product.priceOrder = 0
            this.product.defaultOrder = 1
            break
          case 2:
            if (!this.bar[this.index].sort) {
              console.log('降序')
              this.product.timeOrder = 2
              this.product.priceOrder = 0
              this.product.defaultOrder = 0
            } else {
              console.log('升序')
              this.product.timeOrder = 1
              this.product.priceOrder = 0
              this.product.defaultOrder = 0
            }
            this.bar[this.index].sort = !this.bar[this.index].sort
            this.getProduct(this, null)
            break
          case 3:
            if (!this.bar[this.index].sort) {
              console.log('降序')
              this.product.timeOrder = 0
              this.product.priceOrder = 2
              this.product.defaultOrder = 0
            } else {
              console.log('升序')
              this.product.timeOrder = 0
              this.product.priceOrder = 1
              this.product.defaultOrder = 0
            }
            this.bar[this.index].sort = !this.bar[this.index].sort
            this.getProduct(this, null)
            break
        }
      }
    },
    // handleSwiper (index) {
    //   switch (index) {
    //     case 0:
    //       this.product.timeOrder = 0
    //       this.product.priceOrder = 0
    //       this.product.defaultOrder = 0
    //       break
    //     case 1:
    //       this.product.timeOrder = 0
    //       this.product.priceOrder = 0
    //       this.product.defaultOrder = 1
    //       break
    //     case 2:
    //       this.product.priceOrder = 0
    //       this.product.defaultOrder = 0
    //       if (this.bar[index]) {
    //         this.product.timeOrder = 2
    //       } else {
    //         this.product.timeOrder = 1
    //       }
    //       break
    //     case 3:
    //       this.product.timeOrder = 0
    //       this.product.defaultOrder = 0
    //       if (this.bar[index]) {
    //         this.product.priceOrder = 2
    //       } else {
    //         this.product.priceOrder = 1
    //       }
    //       break
    //   }
    //   this.getProduct(this, null)
    // },
    handleSelect (item) {
      this.option = item.name
      this.product.type = item.id
      this.select = false
      this.getProduct(this)
    },
    handleSaveData (item) {
      this.$localStorage.set('goods', JSON.stringify(item))
    },
    ...mapMutations({
      getProduct: 'getProduct'
    })
  }
}



